<template>
  <b-sidebar v-model="open" type="is-white" :fullheight="true" :overlay="false" :right="true" :position="position">
    <b-menu>
      <b-menu-list>
        <article v-for="(item, index) in allmessages" :key="index" class="tile is-child notification is-cwhite-1">
          <div class="content">
            <b-taglist>
              <b-tag v-if="item.header.length > 1" type="is-cred-2">{{ item.header }}</b-tag>
              <b-tag type="is-cgreen-3">{{ item.author }}</b-tag>
            </b-taglist>
            <p>
              <small>{{ item.content }}</small>
            </p>
            <div class="level">
              <div class="level level-left">
                <b-icon icon="camera-timer" class="level level-item" type="is-cblue-1"></b-icon>
              </div>
              <div class="level level-right">
                <p class="level level-item">{{ item.sendtime }}</p>
              </div>
            </div>
          </div>
        </article>
      </b-menu-list>
    </b-menu>
    <div class="media-right">
      <button class="delete"></button>
    </div>
  </b-sidebar>
</template>

<script>
export default {
  name: 'SideMessage',
  data() {
    return {
      open: false,
      position: 'fixed',

      /**
       * 所有站内信
       */
      allmessages: []
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    /**
     * 初始化
     */
    init() {
      this.allmessages = this.$store.state.common.allmessages
    },
    /**
     * 打开边框站内信
     */
    openSide() {
      this.open = true
    }
  }
}
</script>

