<template>
  <aside class="aside is-placed-left is-expanded">
    <aside-tools :is-main-menu="true">
      <span slot="label">
        <b>RoWarlock</b>
      </span>
    </aside-tools>
    <div class="menu is-menu-main">
      <template v-for="(menuGroup, index) in menu">
        <p v-if="typeof menuGroup === 'string'" :key="index" class="menu-label">{{ menuGroup }}</p>
        <aside-menu-list v-else :key="index" :menu="menuGroup" @menu-click="menuClick" />
      </template>
    </div>
  </aside>
</template>

<script>
import AsideTools from '@/components/Layout/AsideTools'
import AsideMenuList from '@/components/Layout/AsideMenuList'

export default {
  name: 'AsideMenu',
  components: {
    AsideMenuList,
    AsideTools
  },
  props: {
    menu: {
      type: Array,
      default: () => []
    }
  },
  computed: {},
  methods: {
    menuClick(item) {
      this.$emit('menu-click', item)
    }
  }
}
</script>
