<template>
  <div>
    <nav-bar :navmenu="topnav" />
    <aside-menu :menu="sidemenu" />
    <transition name="fade" mode="out-in">
      <router-view :key="$route.path" />
    </transition>
    <footer-bar class="footpadding" />
  </div>
</template>

<script>
import NavBar from '@/components/Layout/NavBar'
import AsideMenu from '@/components/Layout/AsideMenu'
import FooterBar from '@/components/Layout/FooterBar'

export default {
  /**
   * 页面框体，透过框体展示程序
   */
  name: 'Layout',
  components: {
    FooterBar,
    AsideMenu,
    NavBar
  },
  data() {
    return {
      /**
       * 边界菜单
       */
      sidemenu: [
        '通用',
        [
          {
            to: '/',
            icon: 'desktop-mac',
            label: 'Dashboard'
          }
        ],
        '工作台',
        [
          {
            label: '项目',
            icon: 'view-list',
            menu: [
              {
                to: '/project/first',
                icon: 'laptop',
                label: 'PCSOFT1'
              },
              {
                to: '/project/second',
                icon: 'arch',
                label: 'PCSOFT2'
              }
            ]
          },
          {
            label: '释放管理',
            icon: 'alert-decagram',
            menu: [
              {
                to: '/release/nowsprint',
                icon: 'reload',
                label: '当前释放版本'
              },
              {
                to: '/release/history',
                icon: 'history',
                label: '历史释放'
              }
            ]
          },
          {
            label: '个人信息',
            icon: 'monitor-cellphone-star',
            menu: [
              {
                to: '/profile/settings',
                icon: 'settings',
                label: '个人设置'
              },
              {
                to: '/profile/tasklist',
                icon: 'chart-bubble',
                label: '个人任务'
              }
              /*  UPDATE: 2021-02-23 不要了
             {
                to: '/profile/message',
                icon: 'email',
                label: '站内信'
              }
              */
            ]
          },
          {
            label: '工作台',
            icon: 'facebook-workplace',
            menu: [
              {
                to: '/craft/interface',
                icon: 'expansion-card',
                label: '接口测试(web)'
              },
              {
                to: '/craft/interfacerun',
                icon: 'run',
                label: '接口测试(web)执行'
              }
            ]
          },
          {
            to: '/charts',
            icon: 'chart-donut-variant',
            label: '报表'
          }
        ],
        '更多',
        [
          {
            to: '/docs/course',
            label: '教程',
            icon: 'concourse-ci'
          },
          {
            to: '/docs/apis',
            label: 'APIs',
            icon: 'api'
          },
          {
            to: '/docs/about',
            label: '关于',
            icon: 'information-outline'
          }
        ]
      ],
      /**
       * 顶部菜单
       */
      topnav: {
        menus: []
      },

      /**
       * 默认展示项目
       */
      defalutprojs: [],

      /**
       *nav上展示个人超链的
       */
      promenu: {
        icon: 'face-profile',
        value: 'Nate 冯',
        menu: [
          {
            to: '/profile/settings',
            icon: 'settings',
            name: '个人设置'
          },
          /* UPDATE: 2021-02-23 不要了
          {
            to: '/profile/message',
            icon: 'email',
            name: '站内信'
          },
           */
          {
            to: '/profile/tasklist',
            icon: 'chart-bubble',
            name: '个人任务'
          }
        ]
      }
    }
  },
  computed: {
    listData() {
      return this.$store.state.twoproject.defaulttwoprojs
    }
  },
  watch: {
    listData(newVal) {
      console.log('变动')
      // 数据变化后，调用b组件内的函数
      this.defalutprojs = newVal
      this.init()
    }
  },
  mounted() {
    this.defalutprojs = this.$store.state.twoproject.defaulttwoprojs
    this.init()
  },
  methods: {
    /**
     * 初始化
     */
    init() {
      const tempmenu = this.createMenu()
      this.topnav.menus = []
      this.topnav.menus.push(this.promenu) // 推入一个
      // 推入第二个
      this.topnav.menus.push({
        icon: 'menu',
        value: '菜单',
        menu: tempmenu
      })
      this.sidemenu[3][0].menu[0].label = tempmenu[0].name
      this.sidemenu[3][0].menu[1].label = tempmenu[1].name
    },

    /**
     * 创建菜单
     */
    createMenu() {
      const menu = [
        {
          to: '/project/first',
          icon: 'microsoft',
          name: this.defalutprojs[0].name
        },
        {
          to: '/project/second',
          icon: 'arch',
          name: this.defalutprojs[1].name
        },
        {
          to: '/release/nowsprint',
          icon: 'reload',
          name: '当前版本释放'
        },
        {
          to: '/release/history',
          icon: 'history',
          name: '历史版本清单'
        }
      ]
      return menu
    }
  }
}
</script>

<style scoped>
.footpadding {
  padding: 1.5rem 1.5rem 1.5rem;
}
</style>
